<script>
  module.exports = {
    componentName: "formexmple",
    data() {
      return {
        form: {
          btns: [
            {
              label: "查询",
              action: "search"
            }
          ],
          data: [
            {
              type: "input",
              label: "申请时间",
              field: "ipu",
              value: "",
              placeholder: "全部",
              clearable: true
            },
            {
              type: "select",
              label: "收件方式",
              field: "receiptType",
              value: "",
              placeholder: "全部",
              options: [
                {
                  label: "邮寄",
                  value: "1"
                },
                {
                  label: "电子邮箱",
                  value: "2"
                },
                {
                  label: "自取",
                  value: "3"
                }
              ],
              filter: true,
              clearable: true
            },
            {
              type: "datetimerange",
              label: "申请时间",
              field: "date",
              value: ["2020-12-03 00:00:00", "2021-01-23 23:59:59"],
              placeholder: "申请时间",
              clearable: true,
              val2Object: {
                startTime: "",
                endTime: ""
              }
            }
          ]
        }
      };
    },
    methods: {
      search(val) {
        console.log(val);
      }
    }
  };
</script>


