</div></demo-block>
<h2 id="%E5%8F%AF%E8%87%AA%E9%80%82%E5%BA%94%E6%96%87%E6%9C%AC%E9%AB%98%E5%BA%A6%E7%9A%84%E6%96%87%E6%9C%AC%E5%9F%9F">可自适应文本高度的文本域 <a class="cys-markdown-permalink" href="#%E5%8F%AF%E8%87%AA%E9%80%82%E5%BA%94%E6%96%87%E6%9C%AC%E9%AB%98%E5%BA%A6%E7%9A%84%E6%96%87%E6%9C%AC%E5%9F%9F">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-textarea v-model="input1" placeholder="无限自动加高" autosize></cys-textarea>
  <cys-textarea v-model="input2" placeholder="限制从2行到5行超出滚动条" :autosize="{minRows: 2,maxRows: 5}"></cys-textarea>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-textarea</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input1"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"无限自动加高"</span> <span class="hljs-attr">autosize</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-textarea</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-textarea</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input2"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"限制从2行到5行超出滚动条"</span> <span class="hljs-attr">:autosize</span>=<span class="hljs-string">"{minRows: 2,maxRows: 5}"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-textarea</span>&gt;</span>
