<template><section>
<h1>CysModal 对话框</h1>
<h2 id="%E5%9F%BA%E6%9C%AC%E7%94%A8%E6%B3%95">基本用法 <a class="cys-markdown-permalink" href="#%E5%9F%BA%E6%9C%AC%E7%94%A8%E6%B3%95">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-button type="text" @click="modalVisible = true">点击打开 Modal</cys-button>

  <cys-modal title="提示" v-model="modalVisible">
    <span>这是一段信息</span>
    <span slot="footer">
      <cys-button @click="modalVisible = false" plain>取 消</cys-button>
      <cys-button @click="modalVisible = false">确 定</cys-button>
    </span>
  </cys-modal>
</template>


</div>
                            <p>需要设置<code>v-model</code>属性，它接收<code>Boolean</code>，当为<code>true</code>时显示</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"modalVisible = true"</span>&gt;</span>点击打开 Modal<span class="hljs-tag">&lt;/<span class="hljs-name">cys-button</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">cys-modal</span>
    <span class="hljs-attr">title</span>=<span class="hljs-string">"提示"</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"modalVisible"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>这是一段信息<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"footer"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">cys-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"modalVisible = false"</span> <span class="hljs-attr">plain</span>&gt;</span>取 消<span class="hljs-tag">&lt;/<span class="hljs-name">cys-button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">cys-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"modalVisible = false"</span>&gt;</span>确 定<span class="hljs-tag">&lt;/<span class="hljs-name">cys-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-modal</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">modalVisible</span>: <span class="hljs-literal">false</span>
      };
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-button type="text" @click="visible = true">点击打开 Modal</cys-button>

  <cys-modal title="提示" v-model="visible" width="60%" height="60%">
    <span>这是一段信息</span>
    <span slot="footer">
      <cys-button @click="visible = false" plain>取 消</cys-button>
      <cys-button @click="visible = false">确 定</cys-button>
    </span>
  </cys-modal>
</template>


</div>
                            <p><code>width</code>来设置对话框的宽度大小支持百分比和像素设置</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"visible = true"</span>&gt;</span>点击打开 Modal<span class="hljs-tag">&lt;/<span class="hljs-name">cys-button</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">cys-modal</span>
    <span class="hljs-attr">title</span>=<span class="hljs-string">"提示"</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"visible"</span>
    <span class="hljs-attr">width</span>=<span class="hljs-string">"60%"</span>
    <span class="hljs-attr">height</span>=<span class="hljs-string">"60%"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>这是一段信息<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"footer"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">cys-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"visible = false"</span> <span class="hljs-attr">plain</span>&gt;</span>取 消<span class="hljs-tag">&lt;/<span class="hljs-name">cys-button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">cys-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"visible = false"</span>&gt;</span>确 定<span class="hljs-tag">&lt;/<span class="hljs-name">cys-button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-modal</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">visible</span>: <span class="hljs-literal">false</span>
      };
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="cysmodal-attributes">CysModal Attributes <a class="cys-markdown-permalink" href="#cysmodal-attributes">#</a></h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>是否显示 Modal,使用 v-model 绑定才可以图标关闭</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>title</td>
<td>Modal 的标题，也可通过具名 slot （见下表）传入</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>show-close</td>
<td>是否显示关闭按钮</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>width</td>
<td>设置宽度</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="cysmodal-events">CysModal Events <a class="cys-markdown-permalink" href="#cysmodal-events">#</a></h2>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>窗体关闭触发</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="cysmodal-slot">CysModal Slot <a class="cys-markdown-permalink" href="#cysmodal-slot">#</a></h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>—</td>
<td>Modal 的内容</td>
</tr>
<tr>
<td>footer</td>
<td>Modal 按钮操作区的内容</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default {
    data() {
      return {
        modalVisible: false,
        visible: false
      };
    }
  };
</script>


