<h2 id="cystree-attributes">CysTree Attributes <a class="cys-markdown-permalink" href="#cystree-attributes">#</a></h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>展示数据</td>
<td>array</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>childrenKey</td>
<td>子节点对应的字段</td>
<td>string</td>
<td>—</td>
<td>children</td>
</tr>
<tr>
<td>textKey</td>
<td>节点显示文本字段</td>
<td>string</td>
<td>—</td>
<td>label</td>
</tr>
<tr>
<td>indent</td>
<td>节点缩进左侧边距</td>
<td>number</td>
<td>—</td>
<td>18</td>
</tr>
<tr>
<td>showCheckbox</td>
<td>显示复选框</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>defaultExpandAll</td>
<td>默认展开素有的节点</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>autoExpandParent</td>
<td>勾选自动展开父节点</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>showCollapse</td>
<td>显示折叠三角图标</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>filterNode</td>
<td>节点过滤方法</td>
<td>Function(node,data)</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>emptyText</td>
<td>无数据显示文本</td>
<td>string</td>
<td>—</td>
<td>暂无数据</td>
</tr>
<tr>
<td>nodeKey</td>
<td>节点数据唯一标识字段</td>
<td>string</td>
<td>—</td>
<td>id</td>
</tr>
<tr>
<td>defaultCheckedKeys</td>
<td>默认选中节点<code v-pre>node-key</code>对应的数组</td>
<td>array</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>checkParentStrictly</td>
<td>父节点严格模式，勾选子节点，父节点不级联</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>checkChildrenStrictly</td>
<td>子节点严格模式，勾选自父节点，子节点不级联</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>defaultSelectKey</td>
<td>默认选中节点的<code v-pre>node-key</code>对应的值</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>disabledSelect</td>
<td>禁用单行选中</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
</tbody>
</table>
<h2 id="cystree-methods">CysTree Methods <a class="cys-markdown-permalink" href="#cystree-methods">#</a></h2>
<table>
<thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>getNodes</td>
<td>获取所有的节点</td>
<td>—</td>
</tr>
<tr>
<td>setCheckedKeys</td>
<td>设置选中节点，必须设置<code v-pre>node-key</code></td>
<td>(keys, leafOnly=false) 勾选的 keys ，只是叶子节点选中不级联</td>
</tr>
<tr>
<td>setSelectKey</td>
<td>设置选中节点，必须设置<code v-pre>node-key</code></td>
<td>key 要选中节点的 key</td>
</tr>
</tbody>
</table>
<h2 id="cystree-events">CysTree Events <a class="cys-markdown-permalink" href="#cystree-events">#</a></h2>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>node-click</td>
<td>节点被点击时的回调</td>
<td>(node,data,treeNode) 节点包装数据、节点数据、节点本身</td>
</tr>
<tr>
<td>check-change</td>
<td>节点选中状态</td>
<td>(node,data,treeNode) 节点包装数据、节点数据、节点本身</td>
</tr>
<tr>
<td>node-expand</td>
<td>节点展开</td>
<td>(node,data,treeNode) 节点包装数据、节点数据、节点本身</td>
</tr>
<tr>
<td>node-collapse</td>
<td>节点折叠</td>
<td>(node,data,treeNode) 节点包装数据、节点数据、节点本身</td>
</tr>
<tr>
<td>parent-node-check</td>
<td>父节点勾选状态改变触发</td>
<td>(node,data,treeNode) 节点包装数据、节点数据、节点本身</td>
</tr>
</tbody>
</table>
<h2 id="cystree-slot">CysTree Slot <a class="cys-markdown-permalink" href="#cystree-slot">#</a></h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>—</td>
<td>自定义树节点的内容，参数为 { node, data }</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  const data = [{
    label: '一级 1',
    children: [{
      label: '二级 1-1',
      children: [{
        label: '三级 1-1-1'
      }]
    }]
  }, {
    label: '一级 2',
    children: [{
      label: '二级 2-1',
      children: [{
        label: '三级 2-1-1'
      }]
    }, {
      label: '二级 2-2',
      children: [{
        label: '三级 2-2-1'
      }]
    }]
  }, {
    label: '一级 3',
    children: [{
      label: '二级 3-1',
      children: [{
        label: '三级 3-1-1'
      }]
    }, {
      label: '二级 3-2',
      children: [{
        label: '三级 3-2-1'
      }]
    }]
  }];


const data1 = [{
    label: '一级 1',
    children: [{
      label: '二级 1-1',
      children: [{
        label: '三级 1-1-1'
      }]
    }]
  }, {
    label: '一级 2',
    children: [{
      label: '二级 2-1',
      children: [{
        label: '三级 2-1-1'
      }]
    }, {
      label: '二级 2-2',
      children: [{
        label: '三级 2-2-1'
      }]
    }]
  }, {
    label: '一级 3',
    children: [{
      label: '二级 3-1',
      children: [{
        label: '三级 3-1-1'
      }]
    }, {
      label: '二级 3-2',
      children: [{
        label: '三级 3-2-1'
      }]
    }]
  }];

export default {
    data(){
        return {
            data:data,
            data1:data1
        }
    }
}
</script>


