<template><section>
<h1>CysCheckbox 复选框</h1>
<h2 id="%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">基础用法 <a class="cys-markdown-permalink" href="#%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-checkbox v-model="checked">备选项
</cys-checkbox></template>

</div>
                            <p><code>v-model</code>进行双向绑定</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">e-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">checked</span>: <span class="hljs-literal">true</span>
      };
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E7%A6%81%E7%94%A8%E7%8A%B6%E6%80%81">禁用状态 <a class="cys-markdown-permalink" href="#%E7%A6%81%E7%94%A8%E7%8A%B6%E6%80%81">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-checkbox v-model="checked1" disabled>备选项1</cys-checkbox>
  <cys-checkbox v-model="checked2" disabled>备选项</cys-checkbox>
</template>

</div>
                            <p>设置<code>disabled</code>属性即可</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked1"</span> <span class="hljs-attr">disabled</span>&gt;</span>备选项1<span class="hljs-tag">&lt;/<span class="hljs-name">cys-checkbox</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked2"</span> <span class="hljs-attr">disabled</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">cys-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">checked1</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">checked2</span>: <span class="hljs-literal">true</span>
      };
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E5%A4%8D%E9%80%89%E6%A1%86%E7%BB%84">复选框组 <a class="cys-markdown-permalink" href="#%E5%A4%8D%E9%80%89%E6%A1%86%E7%BB%84">#</a></h2>
<p>适用于多个勾选框绑定到同一个数组的情景，通过是否勾选来表示这一组选项中选中的项。</p>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-checkbox-group v-model="checkList">
    <cys-checkbox label="1">复选框 A</cys-checkbox>
    <cys-checkbox label="2">复选框 B</cys-checkbox>
    <cys-checkbox label="3">复选框 C</cys-checkbox>
    <cys-checkbox label="4" disabled>禁用</cys-checkbox>
    <cys-checkbox label="5" disabled>选中且禁用</cys-checkbox>
  </cys-checkbox-group>
</template>


</div>
                            <p><code>cys-checkbox-group</code>元素能把多个 checkbox 管理为一组，只需要在 Group 中使用<code>v-model</code>绑定<code>Array</code>类型的变量即可。 <code>cys-checkbox</code> 的 <code>label</code>属性是该 checkbox 对应的值，若该标签中无内容，则该属性也充当 checkbox 按钮后的介绍。<code>label</code>与数组中的元素值相对应，如果存在指定的值则为选中状态，否则为不选中。</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkList"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span>&gt;</span>复选框 A<span class="hljs-tag">&lt;/<span class="hljs-name">cys-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span>&gt;</span>复选框 B<span class="hljs-tag">&lt;/<span class="hljs-name">cys-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"3"</span>&gt;</span>复选框 C<span class="hljs-tag">&lt;/<span class="hljs-name">cys-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"4"</span> <span class="hljs-attr">disabled</span>&gt;</span>禁用<span class="hljs-tag">&lt;/<span class="hljs-name">cys-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"5"</span> <span class="hljs-attr">disabled</span>&gt;</span>选中且禁用<span class="hljs-tag">&lt;/<span class="hljs-name">cys-checkbox</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">checkList</span>: [<span class="hljs-string">'4'</span>,<span class="hljs-string">'5'</span>]
      };
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="cyscheckbox-attributes">CysCheckbox Attributes <a class="cys-markdown-permalink" href="#cyscheckbox-attributes">#</a></h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>value</td>
<td>当前是否勾选</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>label</td>
<td>复选框在分组中勾选追加到数组的值</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>indeterminate</td>
<td>设置 indeterminate 状态，只负责样式控制</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="cyscheckbox-events">CysCheckbox Events <a class="cys-markdown-permalink" href="#cyscheckbox-events">#</a></h2>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>当绑定值变化时触发的事件</td>
<td>更新后的值</td>
</tr>
</tbody>
</table>
<h2 id="cyscheckbox-slots">CysCheckbox Slots <a class="cys-markdown-permalink" href="#cyscheckbox-slots">#</a></h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>—</td>
<td>复选框文本</td>
</tr>
</tbody>
</table>
<h2 id="cyscheckboxgroup-attributes">CysCheckboxGroup Attributes <a class="cys-markdown-permalink" href="#cyscheckboxgroup-attributes">#</a></h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>value</td>
<td>当前是否勾选</td>
<td>array</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  module.exports = {
    data() {
      return {
        checkList: ['4','5'],
        checked: true,
        checked1: false,
        checked2: true
      };
    }
  };
</script>


