<template><section>
<h1>消息窗口</h1>
<h2 id="%E6%B6%88%E6%81%AF%E6%8F%90%E7%A4%BA">消息提示 <a class="cys-markdown-permalink" href="#%E6%B6%88%E6%81%AF%E6%8F%90%E7%A4%BA">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-button type="text" @click="messageAlert">消息提示</cys-button>
</template>


</div>
                            <p>通过<code>this.$shAlert(message,title,options)</code>或者<code>this.$shAlert(message,options)</code>方法全局调用</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"messageAlert"</span>&gt;</span>消息提示<span class="hljs-tag">&lt;/<span class="hljs-name">cys-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">ModalVisible</span>: <span class="hljs-literal">false</span>
      };
    },
    <span class="hljs-attr">methods</span>:{
      messageAlert(){
        <span class="hljs-keyword">this</span>.$shAlert(<span class="hljs-string">"提示内容"</span>,<span class="hljs-string">"标题"</span>,{
          <span class="hljs-attr">callback</span>:<span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span>{
            alert(<span class="hljs-string">"回调函数"</span>)
          }
        })
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E7%A1%AE%E8%AE%A4%E6%B6%88%E6%81%AF">确认消息 <a class="cys-markdown-permalink" href="#%E7%A1%AE%E8%AE%A4%E6%B6%88%E6%81%AF">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-button type="text" @click="messageConfirm">确认消息</cys-button>
</template>


</div>
                            <p>通过<code>this.$shConfirm(message,title,options)</code>或者<code>this.$shConfirm(message,options)</code>方法全局调用</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"messageConfirm"</span>&gt;</span>确认消息<span class="hljs-tag">&lt;/<span class="hljs-name">cys-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">ModalVisible</span>: <span class="hljs-literal">false</span>
      };
    },
    <span class="hljs-attr">methods</span>:{
      messageConfirm(){
        <span class="hljs-keyword">this</span>.$shConfirm(<span class="hljs-string">"是否确认操作"</span>,<span class="hljs-string">"提示信息"</span>,{
          <span class="hljs-attr">callback</span>:<span class="hljs-function"><span class="hljs-params">type</span>=&gt;</span>{
            <span class="hljs-keyword">if</span>(type===<span class="hljs-string">"confirm"</span>){
              alert(<span class="hljs-string">"确定操作"</span>)
            }<span class="hljs-keyword">else</span>{
              alert(<span class="hljs-string">"取消操作"</span>)
            }
          }
        })
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E5%8D%95%E7%8B%AC%E5%BC%95%E7%94%A8">单独引用 <a class="cys-markdown-permalink" href="#%E5%8D%95%E7%8B%AC%E5%BC%95%E7%94%A8">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-button type="text" @click="messagebox">单独引用</cys-button>
</template>


</div>
                            <p>引入文件通过<code>MessageBox.alert</code>和<code>MessageBox.confirm</code>单独调用</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"messagebox"</span>&gt;</span>单独引用<span class="hljs-tag">&lt;/<span class="hljs-name">cys-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span>  MessageBox  <span class="hljs-keyword">from</span> <span class="hljs-string">'cys-message-box.js'</span>;
MessageBox.alert(<span class="hljs-string">"提示内容"</span>,<span class="hljs-string">"标题"</span>,{
  callback(){
    alert(<span class="hljs-string">"回调函数"</span>)
  }
})
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="options">Options <a class="cys-markdown-permalink" href="#options">#</a></h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>MessageBox 标题</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>message</td>
<td>MessageBox 消息正文内容</td>
<td>string / VNode</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>callback</td>
<td>MessageBox 关闭后的回调</td>
<td>function(action)，action 的值为'confirm'或'cancel'</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>showClose</td>
<td>MessageBox 是否显示右上角关闭按钮</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>cancelText</td>
<td>取消按钮的文本内容</td>
<td>string</td>
<td>—</td>
<td>取消</td>
</tr>
<tr>
<td>confirmText</td>
<td>确定按钮的文本内容</td>
<td>string</td>
<td>—</td>
<td>确定</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  import  MessageBox  from '../../src/components/cys-message-box/cys-message-box.js';
  export default {
    data() {
      return {
        ModalVisible: false
      };
    },
    methods:{
      messageAlert(){
        this.$shAlert("提示内容","标题",{
          callback(){
            alert("回调函数")
          }
        })
      },
      messageConfirm(){
         this.$shConfirm("是否确认操作","提示信息",{
          callback(type){
            if(type==="confirm"){
              alert("确定操作")
            }else{
              alert("取消操作")
            }
          }
        })
      },
      messagebox(){
          MessageBox.alert("提示内容","标题",{
            callback(){
              alert("回调函数")
            }
          })
      }
    }
  };
</script>


