<template><section>
<h1>CysRadio 单选框</h1>
<h2 id="%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">基础用法 <a class="cys-markdown-permalink" href="#%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-radio-group v-model="radio">
    <cys-radio label="1">备选项</cys-radio>
    <cys-radio label="2">备选项</cys-radio>
  </cys-radio-group>
</template>


</div>
                            <p>要使用 Radio 组件，只需要设置<code>v-model</code>绑定变量，选中意味着变量的值为相应 Radio <code>label</code>属性的值，<code>label</code>可以是<code>String</code>、<code>Number</code>或<code>Boolean</code>。</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-radio-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">cys-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">cys-radio</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">radio</span>: <span class="hljs-string">'1'</span>
      };
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="%E7%A6%81%E7%94%A8%E7%8A%B6%E6%80%81">禁用状态 <a class="cys-markdown-permalink" href="#%E7%A6%81%E7%94%A8%E7%8A%B6%E6%80%81">#</a></h3>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-radio-group v-model="radio1">
    <cys-radio label="1" disabled>备选项1</cys-radio>
    <cys-radio label="2" disabled>备选项2</cys-radio>
  </cys-radio-group>
</template>


</div>
                            <p>只要在<code>cys-radio</code>元素中设置<code>disabled</code>属性即可，它接受一个<code>Boolean</code>，<code>true</code>为禁用。</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-radio-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio1"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">disabled</span>&gt;</span>备选项1<span class="hljs-tag">&lt;/<span class="hljs-name">cys-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span> <span class="hljs-attr">disabled</span>&gt;</span>备选项2<span class="hljs-tag">&lt;/<span class="hljs-name">cys-radio</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">radio1</span>: <span class="hljs-string">'1'</span>
      };
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="cysradio-attributes">CysRadio Attributes <a class="cys-markdown-permalink" href="#cysradio-attributes">#</a></h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>Radio 的 value</td>
<td>string / number / boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="cysadio-slots">Cysadio Slots <a class="cys-markdown-permalink" href="#cysadio-slots">#</a></h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>—</td>
<td>单选框文本</td>
</tr>
</tbody>
</table>
<h2 id="cysradiogroup-attributes">CysRadioGroup Attributes <a class="cys-markdown-permalink" href="#cysradiogroup-attributes">#</a></h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>当前是否勾选</td>
<td>string / number / boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  module.exports = {
    data() {
      return {
        radio: '1',
        radio1: '1'
      };
    }
  };
</script>


