<template><section>

<h1>CysSelect 下拉框</h1>
<h2 id="%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">基础用法 <a class="cys-markdown-permalink" href="#%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-select v-model="value" placeholder="请选择">
    <cys-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
    </cys-option>
  </cys-select>
</template>


</div>
                            <p><code>v-model</code>的值为当前被选中的<code>cys-option</code>的 value 属性值</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-select</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请选择"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-option</span>
      <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in options"</span>
      <span class="hljs-attr">:key</span>=<span class="hljs-string">"item.value"</span>
      <span class="hljs-attr">:label</span>=<span class="hljs-string">"item.label"</span>
      <span class="hljs-attr">:value</span>=<span class="hljs-string">"item.value"</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">cys-option</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-select</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">options</span>: [{
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项1'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'黄金糕'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项2'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'双皮奶'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项3'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'蚵仔煎'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项4'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'龙须面'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项5'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'北京烤鸭'</span>
        }],
        <span class="hljs-attr">value</span>: <span class="hljs-string">''</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E7%A6%81%E7%94%A8%E7%8A%B6%E6%80%81">禁用状态 <a class="cys-markdown-permalink" href="#%E7%A6%81%E7%94%A8%E7%8A%B6%E6%80%81">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-select v-model="value1" disabled placeholder="请选择">
    <cys-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
    </cys-option>
  </cys-select>
</template>


</div>
                            <p>为<code>cys-select</code>设置<code>disabled</code>属性，则整个选择器不可用</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-select</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请选择"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-option</span>
      <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in options"</span>
      <span class="hljs-attr">:key</span>=<span class="hljs-string">"item.value"</span>
      <span class="hljs-attr">:label</span>=<span class="hljs-string">"item.label"</span>
      <span class="hljs-attr">:value</span>=<span class="hljs-string">"item.value"</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">cys-option</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-select</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">options</span>: [{
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项1'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'黄金糕'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项2'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'双皮奶'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项3'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'蚵仔煎'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项4'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'龙须面'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项5'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'北京烤鸭'</span>
        }],
        <span class="hljs-attr">value1</span>: <span class="hljs-string">''</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="%E8%87%AA%E5%AE%9A%E4%B9%89%E6%A8%A1%E6%9D%BF">自定义模板 <a class="cys-markdown-permalink" href="#%E8%87%AA%E5%AE%9A%E4%B9%89%E6%A8%A1%E6%9D%BF">#</a></h3>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-select v-model="value2" placeholder="请选择">
    <cys-option v-for="item in cities" :key="item.value" :label="item.label" :value="item.value">
      <span style="float: left">{{ item.label }}</span>
      <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
    </cys-option>
  </cys-select>
</template>


</div>
                            <p>将自定义的 HTML 模板插入<code>cys-option</code>的 slot 中即可。</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-select</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请选择"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-option</span>
      <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in cities"</span>
      <span class="hljs-attr">:key</span>=<span class="hljs-string">"item.value"</span>
      <span class="hljs-attr">:label</span>=<span class="hljs-string">"item.label"</span>
      <span class="hljs-attr">:value</span>=<span class="hljs-string">"item.value"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"float: left"</span>&gt;</span>{{ item.label }}<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"float: right; color: #8492a6; font-size: 13px"</span>&gt;</span>{{ item.value }}<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">cys-option</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-select</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">cities</span>: [{
          <span class="hljs-attr">value</span>: <span class="hljs-string">'Beijing'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'北京'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'Cysanghai'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'上海'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'Nanjing'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'南京'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'Chengdu'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'成都'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'Cysenzhen'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'深圳'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'Guangzhou'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'广州'</span>
        }],
        <span class="hljs-attr">value2</span>: <span class="hljs-string">''</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E5%8F%AF%E6%90%9C%E7%B4%A2">可搜索 <a class="cys-markdown-permalink" href="#%E5%8F%AF%E6%90%9C%E7%B4%A2">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-select v-model="value3" :filter="true" placeholder="请选择">
    <cys-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
    </cys-option>
  </cys-select>
</template>


</div>
                            <p>为<code>cys-select</code>设置<code>filter</code>为<code>true</code>则可进行搜索 label 内容的选项</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-select</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value3"</span> <span class="hljs-attr">:filter</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请选择"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-option</span>
      <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in options"</span>
      <span class="hljs-attr">:key</span>=<span class="hljs-string">"item.value"</span>
      <span class="hljs-attr">:label</span>=<span class="hljs-string">"item.label"</span>
      <span class="hljs-attr">:value</span>=<span class="hljs-string">"item.value"</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">cys-option</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-select</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">options</span>: [{
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项1'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'黄金糕'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项2'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'双皮奶'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项3'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'蚵仔煎'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项4'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'龙须面'</span>
        }, {
          <span class="hljs-attr">value</span>: <span class="hljs-string">'选项5'</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">'北京烤鸭'</span>
        }],
        <span class="hljs-attr">value3</span>: <span class="hljs-string">''</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="cysselect-attributes">CysSelect Attributes <a class="cys-markdown-permalink" href="#cysselect-attributes">#</a></h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>绑定选中的 vue 值</td>
<td>—</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>placeholder</td>
<td>占位符</td>
<td>string</td>
<td>—</td>
<td>请选择</td>
</tr>
<tr>
<td>filter</td>
<td>是否可搜索</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="cysoption-attributes">CysOption Attributes <a class="cys-markdown-permalink" href="#cysoption-attributes">#</a></h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>选项的值</td>
<td>string/number/object</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>label</td>
<td>选项的标签，若不设置则默认与 <code v-pre>value</code> 相同</td>
<td>string/number</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2 id="cysoption-slots">CysOption Slots <a class="cys-markdown-permalink" href="#cysoption-slots">#</a></h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>选项的文本内容</td>
</tr>
</tbody>
</table>
</section></template>
<style lang="stylus" scoped>
  @import '../styles/cys-select';
</style>
<script>
  export default {
    data() {
      return {
        options: [{
          value: '选项1',
          label: '黄金糕'
        }, {
          value: '选项2',
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }, {
          value: '选项4',
          label: '龙须面'
        }, {
          value: '选项5',
          label: '北京烤鸭'
        }],
        value: '',
        value1:'',
        value2:'',
        value3:'',
        cities: [{
          value: 'Beijing',
          label: '北京'
        }, {
          value: 'Cysanghai',
          label: '上海'
        }, {
          value: 'Nanjing',
          label: '南京'
        }, {
          value: 'Chengdu',
          label: '成都'
        }, {
          value: 'Cysenzhen',
          label: '深圳'
        }, {
          value: 'Guangzhou',
          label: '广州'
        }]
      }
    }
  }
</script>


