          <span class="hljs-attr">name</span>: [{ <span class="hljs-attr">required</span>: <span class="hljs-literal">true</span>, <span class="hljs-attr">message</span>: <span class="hljs-string">"不能为空"</span>, <span class="hljs-attr">trigger</span>: <span class="hljs-string">"blur"</span> }],
          <span class="hljs-attr">mail</span>: [
            { <span class="hljs-attr">required</span>: <span class="hljs-literal">true</span>, <span class="hljs-attr">message</span>: <span class="hljs-string">"不能为空"</span>, <span class="hljs-attr">trigger</span>: <span class="hljs-string">"blur"</span> },
