    &gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"dd"</span> <span class="hljs-attr">slot-scope</span>=<span class="hljs-string">"scope"</span>&gt;</span>
      固定列<span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"deleted"</span>&gt;</span>删除<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">a</span>&gt;</span>添加<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-table</span>
  &gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">options2</span>: {
          <span class="hljs-attr">isPagination</span>: <span class="hljs-literal">false</span>,
          <span class="hljs-attr">thead</span>: {
            <span class="hljs-attr">column</span>: [
              {
                <span class="hljs-attr">label</span>: <span class="hljs-string">"表头列一"</span>,
                <span class="hljs-attr">prop</span>: <span class="hljs-string">"aa"</span>
              },
              {
                <span class="hljs-attr">label</span>: <span class="hljs-string">"表头列二"</span>,
                <span class="hljs-attr">prop</span>: <span class="hljs-string">"bb"</span>
              },
              {
                <span class="hljs-attr">label</span>: <span class="hljs-string">"表头列三"</span>,
                <span class="hljs-attr">prop</span>: <span class="hljs-string">"cc"</span>,
                <span class="hljs-attr">fixed</span>: <span class="hljs-string">"right"</span>,
              },
              {
                <span class="hljs-attr">label</span>: <span class="hljs-string">"操作"</span>,
                <span class="hljs-attr">prop</span>: <span class="hljs-string">"dd"</span>,
                <span class="hljs-attr">slotName</span>: <span class="hljs-string">"dd"</span>,
                <span class="hljs-attr">fixed</span>: <span class="hljs-string">"right"</span>,
                <span class="hljs-attr">style</span>: {
                  <span class="hljs-attr">width</span>: <span class="hljs-string">"200px"</span>
                }
              }
            ]
          },
          <span class="hljs-attr">method</span>: {
            <span class="hljs-attr">interface</span>: <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
              <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">errorCode</span>: <span class="hljs-string">"0"</span>,
                <span class="hljs-attr">data</span>: [
                  {
                    <span class="hljs-attr">aa</span>: <span class="hljs-string">"列1一"</span>,
                    <span class="hljs-attr">bb</span>: <span class="hljs-string">"列1二"</span>,
                    <span class="hljs-attr">cc</span>: <span class="hljs-string">"列1三"</span>,
                    <span class="hljs-attr">dd</span>: <span class="hljs-string">"cz"</span>
                  },
                  {
                    <span class="hljs-attr">aa</span>: <span class="hljs-string">"列2一"</span>,
                    <span class="hljs-attr">bb</span>: <span class="hljs-string">"列2二"</span>,
                    <span class="hljs-attr">cc</span>: <span class="hljs-string">"列2三"</span>,
                    <span class="hljs-attr">dd</span>: <span class="hljs-string">"cz"</span>
                  },
                  {
                    <span class="hljs-attr">aa</span>: <span class="hljs-string">"列3一"</span>,
                    <span class="hljs-attr">bb</span>: <span class="hljs-string">"列3二"</span>,
                    <span class="hljs-attr">cc</span>: <span class="hljs-string">"列3三"</span>,
                    <span class="hljs-attr">dd</span>: <span class="hljs-string">"cz"</span>
                  }
                ]
              };
            }, <span class="hljs-comment">// 调用表格数据接口方法</span>
            <span class="hljs-attr">parmas</span>: {}, <span class="hljs-comment">// 参数</span>
            <span class="hljs-attr">cVal</span>: <span class="hljs-number">0</span>, <span class="hljs-comment">// 接口返回参数值 默认0</span>
            <span class="hljs-attr">cKey</span>: <span class="hljs-string">"errorCode"</span>, <span class="hljs-comment">// 接口返回参数key 默认errorCode</span>
            <span class="hljs-attr">failed</span>: <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {}, <span class="hljs-comment">// 接口失败回调</span>
