<template><section>

<h1>CysDateTime 日期时间</h1>
<h2 id="%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">基础用法 <a class="cys-markdown-permalink" href="#%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
    <cys-date-picker v-model="date" placeholder="选择日期"></cys-date-picker>
    <cys-date-picker v-model="date1" placeholder="选择日期" :show-icon="false"></cys-date-picker>
</template>

</div>
                            <p><code>cys-date-picker</code>基础的日期选择</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-date-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"date"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择日期"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-date-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-date-picker</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"date1"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择日期"</span> <span class="hljs-attr">:show-icon</span>=<span class="hljs-string">"false"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-date-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data(){
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">date</span>:<span class="hljs-string">'2222-02-22'</span>,
                <span class="hljs-attr">date1</span>:<span class="hljs-string">''</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E7%A6%81%E7%94%A8%E7%8A%B6%E6%80%81">禁用状态 <a class="cys-markdown-permalink" href="#%E7%A6%81%E7%94%A8%E7%8A%B6%E6%80%81">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
    <cys-date-picker placeholder="选择日期" disabled></cys-date-picker>
</template>
</div>
                            <p>通过 <code>disabled</code> 属性指定是否禁用日期选择</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-date-picker</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择日期"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-date-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E6%97%A5%E6%9C%9F%E9%80%89%E6%8B%A9">日期选择 <a class="cys-markdown-permalink" href="#%E6%97%A5%E6%9C%9F%E9%80%89%E6%8B%A9">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
    <cys-date-picker placeholder="选择年份" type="year"></cys-date-picker>
    <cys-date-picker placeholder="选择月份" type="month"></cys-date-picker>
    <cys-date-picker placeholder="选择日期" type="date"></cys-date-picker>
</template>
</div>
                            <p>通过 <code>type</code> 指定选择模式<code>date</code> <code>month</code> <code>year</code></p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-date-picker</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择年份"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"year"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-date-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-date-picker</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择月份"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"month"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-date-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-date-picker</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择日期"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"date"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-date-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E6%97%A5%E6%9C%9F%E7%A6%81%E7%94%A8">日期禁用 <a class="cys-markdown-permalink" href="#%E6%97%A5%E6%9C%9F%E7%A6%81%E7%94%A8">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
    <cys-date-picker placeholder="选择年份" type="year" :disabled-date="calendarDisabledDate"></cys-date-picker>
    <cys-date-picker placeholder="选择月份" type="month" :disabled-date="calendarDisabledDate"></cys-date-picker>
    <cys-date-picker placeholder="选择日期" type="date" :disabled-date="calendarDisabledDate"></cys-date-picker>
</template>


</div>
                            <p>设置<code>disabled-date</code>属性，方法返回<code>true</code>禁用，参数<code>moment</code> 可以参照相关的文档 <a href="http://momentjs.cn/docs/">moment</a></p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-date-picker</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择年份"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"year"</span> <span class="hljs-attr">:disabled-date</span>=<span class="hljs-string">"calendarDisabledDate"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-date-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-date-picker</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择月份"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"month"</span> <span class="hljs-attr">:disabled-date</span>=<span class="hljs-string">"calendarDisabledDate"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-date-picker</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-date-picker</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择日期"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"date"</span> <span class="hljs-attr">:disabled-date</span>=<span class="hljs-string">"calendarDisabledDate"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-date-picker</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">methods</span>:{
            calendarDisabledDate(current){
                <span class="hljs-comment">// 小于当前日期的禁用</span>
                <span class="hljs-keyword">return</span>  current.isBefore(moment().endOf(<span class="hljs-string">'day'</span>)) ;
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E6%97%B6%E9%97%B4%E9%80%89%E6%8B%A9">时间选择 <a class="cys-markdown-permalink" href="#%E6%97%B6%E9%97%B4%E9%80%89%E6%8B%A9">#</a></h2>
