    <span class="hljs-tag">&lt;<span class="hljs-name">cys-date-picker</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"选择时间"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"datetime"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-date-picker</span>&gt;</span>
