    <span class="hljs-tag">&lt;<span class="hljs-name">cys-calendar</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"year"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-calendar</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-calendar</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"month"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-calendar</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-calendar</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"date"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-calendar</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">cys-calendar</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"time"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-calendar</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E6%97%A5%E5%8E%86%E7%A6%81%E7%94%A8">日历禁用 <a class="cys-markdown-permalink" href="#%E6%97%A5%E5%8E%86%E7%A6%81%E7%94%A8">#</a></h2>
