    <span class="hljs-tag">&lt;<span class="hljs-name">cys-calendar</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"date"</span> <span class="hljs-attr">:disabled-date</span>=<span class="hljs-string">"calendarDisabledDate"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-calendar</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">methods</span>:{
