<h2 id="cysdatepicker-attributes">CysDatePicker Attributes <a class="cys-markdown-permalink" href="#cysdatepicker-attributes">#</a></h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>时间选择模式</td>
<td>string</td>
<td>year/month/date/time</td>
<td>date</td>
</tr>
<tr>
<td>disabled</td>
<td>禁用</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>readonly</td>
<td>是否只读</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>placeholder</td>
<td>输入框占位文本</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>value</td>
<td>绑定值</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>format</td>
<td>日期格式化，参照 moment.js 的格式化规则</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>disabled-date</td>
<td>禁用部分日期选择,参数为<a href="http://momentjs.cn/docs/">moment</a>对象，返回 true 禁用该日期</td>
<td>function(currnetDate)</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>show-icon</td>
<td>是否显示图标</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
</tbody>
</table>
