<template><section>

<h1>CysInput 输入框</h1>
<h2 id="%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">基础用法 <a class="cys-markdown-permalink" href="#%E5%9F%BA%E7%A1%80%E7%94%A8%E6%B3%95">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-input v-model="input" placeholder="请输入内容"></cys-input>
</template>


</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cys-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">input</span>: <span class="hljs-string">''</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E7%A6%81%E7%94%A8%E7%8A%B6%E6%80%81">禁用状态 <a class="cys-markdown-permalink" href="#%E7%A6%81%E7%94%A8%E7%8A%B6%E6%80%81">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-input placeholder="请输入内容" v-model="input1" :disabled="true">
  </cys-input>
</template>


</div>
                            <p>通过 <code>disabled</code> 属性指定是否禁用 input 组件</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-input</span>
    <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input1"</span>
    <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"true"</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">input1</span>: <span class="hljs-string">''</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E5%8F%AF%E6%B8%85%E7%A9%BA">可清空 <a class="cys-markdown-permalink" href="#%E5%8F%AF%E6%B8%85%E7%A9%BA">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-input placeholder="请输入内容" v-model="input2" clearable>
  </cys-input>
</template>



</div>
                            <p>使用<code>clearable</code>属性即可得到一个可清空的输入框</p>

                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-input</span>
    <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span>
    <span class="hljs-attr">v-model</span>=<span class="hljs-string">"input2"</span>
    <span class="hljs-attr">clearable</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>


<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">input2</span>: <span class="hljs-string">''</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="icon-%E5%9B%BE%E6%A0%87">Icon 图标 <a class="cys-markdown-permalink" href="#icon-%E5%9B%BE%E6%A0%87">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-input placeholder="请输入警告信息">
      <i slot="prefix" class="cysicon icon-icon_topbar_gaojingtongzhi"></i>
  </cys-input>

  <cys-input placeholder="输入搜索内容">
      <i slot="suffix" class="cysicon icon-icon_shipinjiankong_suofang"></i>
  </cys-input>
</template>
</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入警告信息"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"prefix"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"cysicon icon-icon_topbar_gaojingtongzhi"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-input</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">cys-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"输入搜索内容"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"suffix"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"cysicon icon-icon_shipinjiankong_suofang"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="%E5%A4%8D%E5%90%88%E5%9E%8B%E8%BE%93%E5%85%A5%E6%A1%86">复合型输入框 <a class="cys-markdown-permalink" href="#%E5%A4%8D%E5%90%88%E5%9E%8B%E8%BE%93%E5%85%A5%E6%A1%86">#</a></h2>
<demo-block class="demo-box">
                            <div class="source" slot="source"><template>
  <cys-input placeholder="请输入文本">
      <template slot="prepend">Http://</template>
  </cys-input>

  <cys-input placeholder="请输入内容">
      <template slot="append">.com</template>
  </cys-input>

  <cys-input placeholder="请输入网址">
      <template slot="prepend">www.</template>
      <template slot="append">.com</template>
  </cys-input>
</template>
</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">cys-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入文本"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"prepend"</span>&gt;</span>Http://<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-input</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">cys-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入内容"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"append"</span>&gt;</span>.com<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-input</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">cys-input</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"请输入网址"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"prepend"</span>&gt;</span>www.<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"append"</span>&gt;</span>.com<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">cys-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="cysinput-attributes">CysInput Attributes <a class="cys-markdown-permalink" href="#cysinput-attributes">#</a></h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>绑定值</td>
<td>string / number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>maxlength</td>
<td>原生属性，最大输入长度</td>
<td>number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>minlength</td>
<td>原生属性，最小输入长度</td>
<td>number</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>placeholder</td>
<td>输入框占位文本</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>clearable</td>
<td>是否可清空</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>禁用</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>auto-complete</td>
<td>原生属性，自动补全</td>
<td>string</td>
<td>on, off</td>
<td>off</td>
</tr>
<tr>
<td>name</td>
<td>原生属性</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>readonly</td>
<td>原生属性，是否只读</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2 id="cysinput-slots">CysInput Slots <a class="cys-markdown-permalink" href="#cysinput-slots">#</a></h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>prefix</td>
<td>输入框头部内容，用于设置图标</td>
</tr>
<tr>
<td>suffix</td>
<td>输入框尾部内容，用于设置图标</td>
</tr>
<tr>
<td>prepend</td>
<td>输入框前置内容</td>
</tr>
<tr>
<td>append</td>
<td>输入框后置内容</td>
</tr>
</tbody>
</table>
<h2 id="cysinput-events">CysInput Events <a class="cys-markdown-permalink" href="#cysinput-events">#</a></h2>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>blur</td>
<td>在 Input 失去焦点时触发</td>
<td>(event: Event)</td>
</tr>
<tr>
<td>focus</td>
<td>在 Input 获得焦点时触发</td>
<td>(event: Event)</td>
</tr>
<tr>
<td>change</td>
<td>在 Input 值改变时触发</td>
<td>(value: string | number)</td>
</tr>
<tr>
<td>clear</td>
<td>在点击由 <code v-pre>clearable</code> 属性生成的清空按钮时触发</td>
<td>—</td>
</tr>
</tbody>
</table>
</section></template>
<style lang="stylus" scoped>
  @import '../styles/cys-input';
</style>
<script>
  module.exports = {
    data() {
      return {
        input: '',
        input1:'',
        input2:'鼠标移上去清空图标'
      };
    }
  };
</script>


